.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.custom-button {

  width: 200px;
  align-items: center;
}
*{
  align-items: center;
  padding: 2px;
}
.h1{
  font-size: 2em;
  font-weight: bold;
  color: #3498db;
  text-align: center;
  margin: 20px 0;
}

.link {
  display: inline-block;
  padding: 5px 5px;
  margin: 5px;
  font-size: 16px;
  width: 100px;
  text-align: center;
  text-decoration: none;
  border: 1px solid #777373;
  color: #fff;
  background-color: #3498db;
  cursor: pointer;
  border-radius: 3px;
}
.link:hover {
  background-color: #2980b9;
}